.video {
    // background-image: url(../images/video-poster.png);
    // background-size: cover;
    // background-position: center;
    background-color: $grey;
    position: relative;
    &__poster {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
        transition: transform 0.4s ease 0s;
        z-index: 1;
    }
    &__play-button {
        background-color: $white;
        border-radius: 50%;
        color: $primary-color;
        cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        top: 50%;
        left: 50%;
        width: rem(65);
        height: rem(65);
        transform: translate(-50%, -50%);
        z-index: 3;
        svg {
            width: rem(18);
            height: rem(18);
            transform: translate(8%, 0);
        }
    }
    @include ishover {
        .video__poster {
            transform: scale(1.08);
        }
    }
    @include phone {
        &__play-button {
            width: rem(55);
            height: rem(55);
            svg {
                width: rem(14);
                height: rem(14);
            }
        }
    }
}